body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.disabled {
  opacity: 0.6;
  cursor: not-allowed; }

.genericButton {
  background-color: #d9d9d9;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 7px;
  padding-bottom: 7px;
  border-radius: 35px;
  font-size: 100%;
  border-width: 0px;
  outline: 0px; }

.genericButton:focus {
  outline: 0px; }

.container-fluid {
  padding: 0px;
  margin: 0px; }

.date {
  flex-grow: 1;
  /* flex-basis: 14.20%; */
  min-width: 14.20%;
  max-width: 14.20%;
  margin: 0px;
  border: 1px solid #e6e6e6;
  padding: 0px; }

.allDates {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 10px;
  background-color: white; }

.calenderContainer {
  background-color: #e5d1d2;
  padding: 0px;
  display: flex;
  align-items: center;
  display: flex;
  margin-bottom: 10px; }

.dateRow {
  margin: 10px;
  width: 100vw;
  margin: 10px; }

@media only screen and (max-width: 760px) {
  .taskInCalender {
    padding: 2px;
    border-radius: 5px;
    margin-bottom: 5px;
    font-size: 90%;
    word-break: normal;
    word-wrap: break-word; } }

